import Slider from "react-slick";
import AboutUsCard from "./AboutUsCard";


export default function ResponsiveSlider() {
  var settings = {
    dots: true,
    infinite: true,
    speed: 1000,
    slidesToShow: 3,
    slidesToScroll: 3,
    appendDots: (dots) => {
      return (
        <ul>
          {dots.map((item) => (
            <li
              key={item.key}
              onClick={item?.props?.children?.props?.onClick}
              className={`transition-all  duration-1000`}
              style={{
                backgroundColor: item?.props?.className ? "#124782" : "#8f8f8f",
                width: item?.props?.className ? "30px" : "14px",
                height: "14px",
                borderRadius: "9999px",
              }}
            />
          ))}
        </ul>
      );
    },
    responsive: [
      {
        breakpoint: 1440,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
        },
      },
      {
        breakpoint: 1366,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
        },
      },
      {
        breakpoint: 1100,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
        },
      },
      {
        breakpoint: 700,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
    arrows: false,
  };
  return (
    <Slider
      {...settings}
      draggable={false}
    >
      <AboutUsCard />
      <AboutUsCard />
      <AboutUsCard />
      <AboutUsCard />
      <AboutUsCard />
      <AboutUsCard />
      <AboutUsCard />
      <AboutUsCard />
    </Slider>
  );
}
